
import React, { useState } from 'react';
import Button from '@mui/material/Button';

function FeedbackModal({response}) {
        const [value, setValue] = useState(0);
    

        function changeValue(event) {
           setValue(event.target.value);
        }
     console.log(value);
    return (
        <>
        <div className='feedback-modal'>
            <div className='feedback-modal-header'>
                <p className='text-feedback'>Отзыв</p>
            </div>
           
        <div className='feedback-info'>
            <img alt="avatar" src={response?.extra_data?.avatar } className="login-btn"/>
            <div className='text-feedback'>Вы вошли как {response?.extra_data?.personaname}</div>
        </div>
        <div className='feedback-form'>
            <textarea className='textarea' placeholder='Текст' cols="30" rows="10"></textarea>
            <div>Ваша оценка</div>
            <div className="rating-area">
                <input type="radio" id="star-5" name="rating" value="5" onChange={changeValue}/>
                <label for="star-5" title="Оценка «5»"></label>	
                <input type="radio" id="star-4" name="rating" value="4" onChange={changeValue}/>
                <label for="star-4" title="Оценка «4»"></label>    
                <input type="radio" id="star-3" name="rating" value="3" onChange={changeValue}/>
                <label for="star-3" title="Оценка «3»"></label>  
                <input type="radio" id="star-2" name="rating" value="2" onChange={changeValue}/>
                <label for="star-2" title="Оценка «2»"></label>    
                <input type="radio" id="star-1" name="rating" value="1" onChange={changeValue}/>
                <label for="star-1" title="Оценка «1»"></label>
            </div>
            <div className='feedback-button'>
                <Button variant="contained" color="success">Отправить</Button>
            </div>
            </div>
        </div>
    </>
    );
  }

  export default FeedbackModal;